.header__logo{
  width: 142px;
  height: 33px;
  object-fit: cover;
  background-image: url('../../../images/Vector.svg');
}
@media screen and (max-width: 1024px) {
  .header__logo {
    margin-left: 27px;
  }
}
@media screen and (max-width: 425px) {
  .header__logo {
    width: 104px;
    height: 25px;
    background-image: url('../../../images/minilogo.svg');
  }
}